     ul {\n          li {\n            padding: 5px 0;\n          }\n        }\n      }\n    }\n    .info_box {\n      .info_header {\n        width: 100%;\n        display: flex;\n        flex-direction: row;\n        justify-content: space-between;\n        font-size: @contact_font;\n      }\n    }\n  }\n  .sidebar {\n    width: @sidebar_width;\n    height: 100%;\n    flex: 0 0 auto;\n\n    .skills,.classes,.books {\n      page-break-inside: avoid;\n      .skills_table,.classes_table,.books_table {\n        margin-top: -10px;\n        display: flex;\n        flex-direction: column;\n        flex-wrap: nowrap;\n\n        .skill_row,.class_row,.books_row {\n          display: flex;\n          flex-direction: row;\n          flex-wrap: nowrap;\n          justify-content: space-between;\n          font-size: @small_font;\n          padding-top: 10px;\n          overflow: hidden;\n          white-space: nowrap;\n          text-overflow: ellipsis;\n          .class_name {\n            font-size: @small_font;\n          }\n          .class_info {\n            font-size: @small_font;\n            display: flex;\n            flex-direction: row;\n          }\n        }\n        .books_row {\n          font-size: 12px;\n          flex-direction: column;\n          .book_row {\n            display: flex;\n            flex-direction: row;\n            flex-wrap: nowrap;\n            justify-content: space-between;\n            align-items: center;\n\n            .book_title {\n              font-size: 16px;\n              max-width: 70%;\n              overflow: hidden;\n              text-overflow: ellipsis;\n            }\n\n            .book_rating_wrapper {\n              width: 60px;\n              height: 10px;\n              border: 1px solid @base_color;\n\n              .book_rating {\n                height: 10px;\n                background-color: @odd_color;\n              }\n            }\n          }\n        }\n      }\n    }\n  }\n\n  .section_header {\n    padding-top: 10px;n\n    &.align_left {\n      .item_wrapper {\n        justify-content: flex-start;\n        .header_item {\n          padding-right: 10px;\n        }\n      }\n    }\n    .item_wrapper {\n      display: flex;\n      flex-direction: row;\n      flex-wrap: nowrap;\n      justify-content: space-between;\n    }\n    .header_item {\n      display: inline-block;\n      flex-grow: 1;\n      text-align: right;\n    }\n    .styleButton {\n      height: @large_font;\n      line-height: @large_font;\n      padding: 0 5px;\n      font-size: @small_font;\n      display: inline-block;\n      text-align: center;\n      // border: 1px solid @base_color;\n      background-color: @odd_color;\n      border-radius: 7px;\n      z-index: 10;\n      cursor: pointer;\n      -webkit-user-select: none;  /* Chrome all / Safari all */\n      -moz-user-select: none;     /* Firefox all */\n      -ms-user-select: none;      /* IE 10+ */\n      user-select: none;          /* Likely future */\n    }\n    .menu_wrapper {\n      position: relative;\n\n      .options_wrapper {\n        position: absolute;\n        z-index: 1000;\n        right: 0;\n        width: 90px;\n        .option {\n          z-index: 10;\n          width: 100%;\n          background-color: fade(white, 90%);\n          cursor: pointer;\n          &:hover {\n            background-color: darken(fade(white, 90%), 20%);\n          }\n        }\n      }\n    }\n    .header_title {\n      font-size: @large_font;\n      line-height: @large_font;\n      color: @header_color;\n      flex-grow: 0;\n    }\n    hr {\n      opacity: .3;\n      color: @base_color;\n    }\n  }\n  a {\n    text-decoration: none;\n    border-bottom: 2px solid @base_color;\n    color: @base_color;\n    line-height: @base_font + 1;\n    margin: 4px 0;\n  }\n}\n\n.pdf_link {\n  position: absolute;\n  top: 0;\n  right: 0;\n}\n\n@media (max-width: @min_page_width_for_full_layout) {\n  #app {\n    width: 80%;\n    margin: 0 auto;\n    .flexbody {\n      flex-wrap: wrap-reverse;\n    }\n    .sidebar {\n      width: 90%;\n    }\n    .mainbar {\n      min-width: 0;\n      .profile {\n        .section_header {\n          display: none;\n        }\n      }\n    }\n    .name {\n      font-size: @super_large_font * .9;\n    }\n  }\n}\n\n@media (max-width: @small_title_width) {\n  #app {\n    .name {\n      font-size: @super_large_font * .7;\n      white-space: normal;\n      text-align: center;\n    }\n  }\n}\n\n@media print {\n  html {\n    font-size: 50%;\n  }\n  #app {\n    width: 90%;\n\n    .flexheader {\n      flex-wrap: nowrap;\n      flex-direction: row-reverse;\n    }\n\n    .sidebar {\n      margin-top: -20px !important;\n    }\n\n    .section_header {\n      padding-top: 10px !important;\n    }\n\n    .class_row, .skill_row, .book_row {\n      padding-top: 5px !important;\n      &:nth-child(2n+1) {\n        border-bottom: 1px solid @odd_color;\n      }\n      &:nth-child(2n) {\n        border-bottom: 1px solid @odd_color;\n      }\n    }\n\n    .info_box {\n      padding: 5px !important;\n\n      ul {\n        li {\n          padding: 2px 0 !important;\n        }\n      }\n    }\n\n    .classes {\n      display: none;\n    }\n  }\n}\n"],"sourceRoot":""}]);
